@import url("https://fonts.googleapis.com/css2?family=Lato:wght@300&family=Syne+Mono&display=swap");

.main {
  margin: 0 auto;
  margin-top: 0;
  max-width: 1000px;
  @media screen and (min-width: 1600px) {
    max-width: 1400px;
  }
  @media screen and (min-width: 2500px) {
    max-width: 2400px;
  }
  height: 100vh;
}

.title {
  display: flex;
  color: rgb(0, 0, 0);
  font-family: "Lato", sans-serif;
  justify-content: center;
  word-wrap: wrap;
  margin-bottom: 0;
  animation-duration: 0.6s;
  animation-name: slidein;
  animation-timing-function: ease-in-out;
  max-width: 100%;
}

.legend {
  position: absolute;
  object-fit: stretch;
  background: url("../../assets/sunset.png") no-repeat center center fixed;
  background-attachment: fixed;
  background-size: cover;
  margin: 0;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: -1;
}

h1 {
  font-size: 14vw;
}

.error {
  font-size: 1em;
  color: rgb(255, 255, 255);
}

@keyframes slidein {
  from {
    margin-left: 100%;
  }

  to {
    margin-left: 0%;
  }
}
